import { createWebHistory, createRouter } from "vue-router";
import Product from "./components/ProductWrap.vue";
import Service from "./components/ServiceWrap.vue";
import Company from "./components/CompanyWrap.vue";
import qna from "./components/qnaWapper.vue";
const routes = [
  {
    path: "/",
    component: Product,
  },
  {
    path: "/service",
    component: Service,
  },
  {
    path: "/company",
    component: Company,
  },
  {
    path: "/qna",
    component: qna,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});
export default router;
