<template lang="">
  <div id="containter">
    <ul>
      <div class="in_wrap">
        <li
          class="qnaItem contents"
          v-for="(l, i) in list"
          :key="i"
          @click="modalOpen(i)"
        >
          {{ l.title }}
          <qnaModal
            :modal="modal"
            :crt="crt"
            :qna="list"
            @closeModal="modalClose"
          ></qnaModal>
        </li>
      </div>
    </ul>
  </div>
</template>
<script>
import qnaList from "../assets/qnaList.js";
import qnaModal from "./ModalWrap1.vue";

export default {
  components: {
    qnaModal,
  },
  data() {
    return {
      list: qnaList,
      modal: false,
      crt: 0,
    };
  },
  methods: {
    modalOpen(i) {
      this.crt = i;
      this.modal = true;
      console.log(
        "%c=======modalOpen_qnaWrap : index - ",
        "background:blue;color:white",
        i
      );
    },
    modalClose() {
      console.log("%c=======modalClose_qnaWrap", "background:red;color:white");
      this.modal = false;
    },
  },
};
</script>
<style scoped>
#container {
  margin-bottom: 10rem;
}
.in_wrap {
  justify-content: space-around;
  display: flex;
  flex-wrap: wrap;
  cursor: pointer;
}
img {
  /* width: auto; */
  height: auto;
}
h3 {
  color: gray;
  font-size: 1.6rem;
  margin: 3rem 0 1rem;
  position: relative;
}
h3:after {
  display: block;
  content: "";
  width: 10%;
  height: 0.5rem;
  background-color: gainsboro;
  margin: 1rem auto;
}
</style>
